body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

ol {
    width: 30%;
    margin: auto;
}
.Input-field {
    border-radius: 4px 0px 0px 4px;
    border: 1px solid rgb(211, 211, 211);
    width: 40%;
    padding-left: 8px;
}

.Search-button {
    width: 16%;
    text-decoration: none;
    color: white;
    background-color: lightseagreen;
    border-radius: 0px 4px 4px 0px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.Left-container {
    display: flex;
    flex-direction: column;
}

.Pokemon-name {
    font-weight: bold;
}
.Pokemon-container {
    min-height: 50vh;
    display: flex;
    flex-direction: column;
    padding-top: 16px;
}

.Input-container {
    display: flex;
    justify-content: center;
    min-height: 30px;
    padding-bottom: 20px;
}

.Result-container {
    min-height: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.User-table {
    margin: auto;
    padding: 8px 0px;
    border-collapse: collapse;
}

td {
    border: 1px solid #ddd;
    padding: 4px;
}

th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: center;
    background-color: #2f8d88;
    color: white;
    border: 1px solid #ddd;
}

tr:nth-child(even){background-color: #d6fffc;}

tr:hover {background-color: #ddd;}

.link {
    text-decoration: none;
}
.Loading {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 200px;
}

.Detail-table {
    margin: auto;
    padding: 8px 0px;
    border-collapse: collapse;
    text-align: left;
}

td {
    border: 1px solid #ddd;
    padding: 8px;
}

th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: center;
    background-color: #2f8d88;
    color: white;
    border: 1px solid #ddd;
}

.Header-desc {
    width: 150px;
}

.Header-value {
    width: 200px;
}

tr:nth-child(even){background-color: #d6fffc;}

tr:hover {background-color: #ddd;}
.Loading {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 200px;
}

.Link-container {
    display: flex;
    justify-content: space-evenly;
    padding-bottom: 16px;
}

.Link {
    background-color: lightseagreen;
    text-decoration: none;
    padding: 8px 16px;
    border-radius: 4px;
    color: white;
    width: 14%;
}

.App {
  text-align: center;
  background-color: #f1f1f1;
  min-height: 100vh;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 10vmin;
}

.App-header {
  background-color: #282c34;
  height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App-layout {
    margin: auto;
    width: 60vw;
    padding-top: 20px;
    padding-bottom: 40px;
    min-height: 60vh;
}

.App-link {
  color: #61dafb;
}

.App-footer {
  background-color: #282c34;
  height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

